import React, { useState, useMemo, useEffect } from "react";
import { Input, Button } from "antd";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import "./Header.css";
import { color } from "@mui/system";
import axios from "axios";
import SearchPage from "../DetailPage/SearchPage";
import Item from "antd/lib/list/Item";
import { KAKAO_AUTH_URL } from "../../component/OAuth";

const { Search } = Input;

const Header = () => {
  const [inputValue, setInputValue] = useState("");
  const { userData } = useSelector((state) => state.user);
  const navigate = useNavigate();
  let Tokencase = <div></div>;
  const acToken = sessionStorage.getItem("accesstoken");

  const onSearch = (e) => {
    setInputValue(e.target.value);
  };

  const onSubmit = (inputValue) => {
    const url = `/search/${inputValue}`;
    navigate(url);
  };

  const onClickHandle = () => {
    if (window.confirm(" ?")) {
      sessionStorage.removeItem("accesstoken");
      window.location.replace("/");
      alert(" .");
    }
  };

  const SearchSytle = useMemo(
    () => ({
      marginTop: "20px",
      width: "fit-content",
      //float : 'right',
    }),
    []
  );

  const SearchArea = useMemo(
    () => ({
      float: "right",
    }),
    []
  );

  if (acToken === null) {
    {
      /*token  */
    }
    Tokencase = (
      <Button
        href={KAKAO_AUTH_URL}
        style={{
          backgroundColor: "#283858",
          color: "white",
          border: "none",
          fontSize: "18px",
          float: "right",
          marginTop: "20px",
          marginRight: "20px",
        }}
      >
         
      </Button>
    );
  } else {
    {
      /*token  */
    }
    Tokencase = (
      <div>
        <Button
          onClick={() => navigate("/mypage")}
          style={{
            backgroundColor: "#283858",
            color: "white",
            border: "none",
            fontSize: "18px",
            float: "right",
            marginTop: "20px",
            marginRight: "20px",
          }}
        >
          
        </Button>
        <Button
          onClick={() => onClickHandle()}
          style={{
            backgroundColor: "#283858",
            color: "white",
            border: "none",
            fontSize: "18px",
            float: "right",
            marginTop: "20px",
            // marginRight: "20px",
          }}
        >
          
        </Button>
        <Search
          value={inputValue}
          onChange={onSearch}
          placeholder=",  ."
          onSearch={onSubmit}
          style={SearchSytle}
        />
      </div>
    );
  }

  return (
    <header>
      <div className="head2"></div>
      <div className="head">
        <div className="headIn">
          <div style={SearchArea}>
            {/*    ,  /   */}
            {Tokencase}
            {/*  */}
          </div>
          <br />
        </div>
        <h1
          id="title"
          onClick={() => navigate("/")}
          style={{ textAlign: "center", cursor: "pointer", color: "white" }}
        >
          TICKET TOGETHER
        </h1>
      </div>
    </header>
  );
};

export default Header;

