import React from "react";
import { Button } from "antd";
import "./Footer.css";

const Footer = () => {
  const moveToTop = () => {
    document.documentElement.scrollTop = 0;
  };

  return (
    <footer>
      <nav>
        <a href="https://github.com/ticket-together" target="_blank">
          Github
        </a>
      </nav>
      <p>
        <span>2022   </span>
        <br />
        <span></span>
        <span>FrontEnd: , </span>
        <span>BackEnd: , , </span>
      </p>
      {/* Top  */}
      <Button
        shape="circle"
        onClick={moveToTop}
        style={{ position: "fixed", bottom: "60px", right: "80px" }}
      >
        
      </Button>
    </footer>
  );
};

export default Footer;
