import React, { Component, useState, useEffect, useMemo } from "react";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";

import { AutoComplete, Button, Card, Tooltip, Radio } from "antd";
import { SearchOutlined } from "@ant-design/icons";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import { convertLegacyProps } from "antd/lib/button/button";

const InterworkPage = () => {
  const [data, setData] = useState([]);
  const [id, setId] = useState("");
  const [siteId, setSiteId] = useState("");
  const navigate = useNavigate();
  const siteName = "";

  const ButtonStyle = useMemo(
    () => ({
      width: "200px",
      margin: "auto",
      display: "block",
      //padding : '70px',
      marginTop: "30px",
    }),
    []
  );

  useEffect(() => {
    getSiteInfo();
  }, []);

  async function getSiteInfo(siteName) {
    await axios
      .get(`/api/v1/tickets/site/${siteName}`)
      .then((response) => {
        if (
          response.data.result !== "undefined" &&
          response.data.result !== null
        ) {
          setData(response.data.result);
          setSiteId(response.data.result.siteId);
          setId(response.data.result.id);
        }
      })
      .catch((error) => {
        console.log(error);
      }); //
  }

  const onclickHandle = (siteName) => {
    getSiteInfo(siteName);
    if (siteId != null && siteId != "undefined") {
      console.log(siteId);
      alert("  .");
      navigate(`/interwork`);
    } else {
      alert("  .");
      console.log(siteId);
      navigate(`/external/${siteName}`);
    }
  };

  async function onDeleteHandle(siteName) {
    getSiteInfo(siteName);
    await axios
      .delete(`/api/v1/tickets/site/${id}`)
      .then((res) => {
        alert("   .");
        setSiteId(null);
      })
      .catch(function (error) {
        console.log("");
        console.log(error);
      });
  }

  // axios.delete(`http://3.38.7.238:8080/api/v1/tickets/site/`, {
  //   headers: {
  //     Authorization: authorizationToken
  //   },
  //   data: {
  //     source: source
  //   }
  // });

  return (
    <>
      {/* HEADER */}
      <Header />
      <center>
        <Button style={{ width: "300px" }}></Button>
        <br />
        <Button
          danger
          onClick={(e) => {
            onclickHandle("", e);
          }}
        >
           
        </Button>
        <Button
          type="primary"
          onClick={(e) => {
            onDeleteHandle("", e);
          }}
          ghost
        >
          
        </Button>
        <br />
        <br />
        <Button style={{ width: "300px" }}>11</Button>
        <br />
        <Button
          onClick={(e) => {
            onclickHandle("11", e);
          }}
          danger
        >
          11 
        </Button>
        <Button
          type="primary"
          ghost
          onClick={(e) => {
            onDeleteHandle("11", e);
          }}
        >
          
        </Button>
        <br />
        <br />
        <Button style={{ width: "300px" }}></Button>
        <br />
        <Button
          onClick={(e) => {
            onclickHandle("", e);
          }}
          danger
        >
           
        </Button>
        <Button
          type="primary"
          ghost
          onClick={(e) => {
            onDeleteHandle("", e);
          }}
        >
          
        </Button>
      </center>
    </>
  );
};

export default InterworkPage;
